                       :cancelText="$store.state.uuid?'取消':$t('label_The_previous_step')"
                       @cancel='cancel'></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import showDataAccessHeader from '../showDataAccessHeader'
export default {
  components: { showDataAccessHeader },
  props: {
    readonly: {
      type: Boolean,
      default: false
    },
  },
  data () {
    return {
      obj: {
        num: '1'
      }
    };
  },
  validations: {},
  computed: {},
  created () { },
  watch: {},
  mounted () {

  },

  methods: {
    add () {
      this.open = true
    },
    success () {
      if (this.$store.state.uuid) {//修改
        this.$emit('success')
      }
      else {//新增
        this.$emit('success')
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  },
};
</script>

